import {Component, ViewChild} from '@angular/core';
import { NavController, NavParams, Slides } from 'ionic-angular';
import {HKService} from "../../../service/hk.service";
import {StorageService} from "../../../service/storage.service";
import {AlertService} from "../../../service/alert.service";

/**
 * Generated class for the HkHomePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */


@Component({
  selector: 'page-hk-home',
  templateUrl: 'hk-home.html',
})
export class HkHomePage {
  @ViewChild('slides ') slides: Slides;
  private timeTask1;
  private bannerArr = [
    {img: "http://static.ch9588.com/upload/images20190104095637.jpg"},
    {img: "http://static.ch9588.com/upload/images20190215114955.jpg"},
    {img: "http://static.ch9588.com/upload/images20190215115036.jpg"}
  ];
  private typeTable = {};
  private codeArr = ['xglhc', 'jslhc'];//xglhc jslhc
  private showType = 0;
  private redArr = [];
  private greenArr = [];
  private blueArr = [];
  private objectKeys = Object.keys;
  private news = [];
  private showVideo = false;

  constructor(public navCtrl: NavController, public navParams: NavParams, private hkService: HKService,
              private alertService: AlertService, private storageService: StorageService) {
    this.hkService.getServerTime().then((res) => {
      if (res.code == '0') {
        this.storageService.set('timeDiff', (new Date(res.data).getTime() - new Date().getTime()).toString());
      }
    });
    this.getBannerArr();
    this.getTypeTable();
    this.getNewsData();
  }

  getData(event) {
    this.showVideo = event;
  }

  getBannerArr() {
    // let that: any = this;
    //
    // that.gService.getBanner().then((res) => {
    //   if (res.code == 0) {
    //     that.bannerArr = res.data;
    //   } else {
    //     that.alertService.msg(res.msg);
    //   }
    // }).catch(() => {
    //   that.alertService.msg("");
    // });
  }

  /**/
  autoSlide(slider, time) {
    return setInterval(() => {
      if (!slider.isEnd()) {
        slider.slideNext(1000);
      } else {
        slider.slideTo(0, 1000);
      }
    }, time);
  }

  /**/
  startSlide() {
    setTimeout(() => {
      if (this.slides && this.slides.length() > 1 && !this.timeTask1)
        this.timeTask1 = this.autoSlide(this.slides, 3000);
    }, 200);
  }

  /**/
  stopSlide() {
    if (this.timeTask1) {
      clearInterval(this.timeTask1);
      this.timeTask1 = null;
    }
  }

  getTypeTable() {
    let that: any = this;
    that.hkService.getTypeTable().then((rs) => {
      if (rs.code == 0) {
        that.typeTable = rs.data;
        that.redArr = rs.data.color[''].toString().split(",");
        that.blueArr = rs.data.color[''].toString().split(",");
        that.greenArr = rs.data.color[''].toString().split(",");
        that.storageService.setArray("redArr", that.redArr);
        that.storageService.setArray("blueArr", that.blueArr);
        that.storageService.setArray("greenArr", that.greenArr);
      }
    });
  }


  getNewsData() {
    let that: any = this;
    that.hkService.getNewsData('yctj ', '', that.page, 5).then((rs) => {
      if (rs.code == 0) {
        that.news = rs.data;
      }
    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad HkHomePage');
  }

}



